import { FC } from 'react';

import { SIZES } from './Icon.constants';
import { Wrapper } from './Icon.styles';
import { IconProps } from './Icon.types';

const Icon: FC<IconProps> = ({ svg, size = 'base', ...rest }) => {
  const dimension = SIZES[size];

  return <Wrapper {...rest} svg={svg} width={dimension} height={dimension} />;
};

export default Icon;
